<template>
  <div>
    
    <swiper :options="swiperOption" ref="mySwiper">
      <swiper-slide class="slide1">
        <Hello></Hello>
        <TimeAxis></TimeAxis>
      </swiper-slide>
      <swiper-slide>
        <History></History>
      </swiper-slide>
      <swiper-slide>
        <Introduce></Introduce>
      </swiper-slide>
      <swiper-slide>
        <List2></List2>
      </swiper-slide>
      <swiper-slide>
        <List1></List1>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>

import Hello from '../components/hello.vue'
import TimeAxis from '../components/timeAxis.vue'
import History from '../components/history.vue'
import List1 from '../components/list1.vue'
import List2 from '../components/list2.vue'

import Introduce from '../components/introduce.vue'

import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'

import anime from 'animejs'


export default {
  name: 'index',
  components: {
    Hello,
    TimeAxis,
    History,
    Introduce,
    List1,
    List2,
    swiper,
    swiperSlide
  },
  data(){
    return{
      swiperOption: {
        direction: 'vertical', // 垂直切换选项
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        on: {
          slideChangeTransitionEnd: function() {
            let i = this.realIndex+1;
            if(i==3){
              anime({
                  targets: '.inel',
                  opacity: [0, 1],
                  translateY: [200,0],
                  duration: 600,
                  delay: anime.stagger(500),
                  easing: 'easeInOutQuad'
              });
              anime({
                  targets: '.skill-tit',
                  opacity: [0, 1],
                  translateX: [-200,0],
                  delay: 1200,
                  duration: 600,
                  easing: 'easeInOutQuad'
              });
              anime({
                  targets: '.skill span',
                  opacity: [0, 1],
                  scale: [0.1,1],
                  duration: 500,
                  delay: anime.stagger(100, {start: 2000}) ,
                  easing: 'easeOutElastic'
              });
            }
            else if(i!=3){
              anime({
                  targets: '.inel,.skill-tit,.skill span',
                  opacity: 0,
              });
            }
            if(i==5){
              anime({
                  targets: '.slide__img',
                  opacity: [0, 1],
                  translateY: [-600, 0],
                  delay: anime.stagger(100), // 每个元素的延迟增加100毫秒。
                  duration: 1200
              });
            }
            if(i==4){
              anime({
                  targets: '.pic',
                  opacity: [0, 1],
                  scale : [.5, 1],
                  delay: anime.stagger(100) // 每个元素的延迟增加100毫秒。
              });
            }
            else if(i!=4){
              anime({
                  targets: '.pic',
                  opacity: 0,
              });
            }
          }
        }
        
      },
    }
  },
}
</script>

<style>
.bg{
 opacity: 0;
} 
html, body, #app{
  height: 100%;
  background: #fcf2ea;
  
}
.swiper-container{
  width: 100%;
  height: 100%;
  
}
.swiper-slide{
  height: auto;
}
.swiper-pagination{
  position: fixed;
}
.swiper-pagination-bullet-active {
  background: #f06972;
}
</style>
